<template lang="pug">
div#page-container.enable-page-overlay.side-scroll.main-content-narrow
  main#main-container
    div.bg-image(style="background-image: url('./assets/bg_dashboard.jpg');")
      div.bg-primary
        div.content
          div.py-md-0.text-center.text-md-left.js-appear-enabled.animated.fadeIn(data-toggle='appear')
            h1
              span.font-size-h2.mb-2.text-white eGFR予測モデル
    div.content
      div.row
        div.col-lg-6
          div.block.block-rounded.block-bordered
            div.block-content
              div
                div.form-row
                  div.form-group.col-md-3(v-for='exam in exams')
                    label {{exam.title}}
                    div.input-group.mb-2
                      input.form-control(type="number", step="0.01",v-model='exam.value', v-on:blur="onExamUpdated")

                  div.form-group.col-md-2.text-bottom
                    label(style='color: transparent;') Predict
                    div.btn.btn-sm.btn-success(style='width: 100px;',type='button', v-on:click='onClickPredict')
                     | Predict

        div.col-lg-6
          div.block.block-rounded.block-bordered.block-themed.mb-2(v-bind:class='isLoading ? `block-mode-loading` : ``', v-if='alertMessage')
            div.block-header.bg-danger-lighter
              h3.block-title.text-danger Request Error
            div.block-content
              h5 {{ alertMessage }}

          div.block.block-rounded.block-bordered.block-themed.mb-2(v-bind:class='isLoading ? `block-mode-loading` : ``')
            div.block-header.bg-xmodern-light
              h3.block-title Prediction Result
            div.block-content
              table.table.table-sm.table-bordered.table-vcenter(v-if='result')
                thead.thead-light
                  tr
                    th.text-center Result
                tbody
                  tr.text-center
                    img(:src="result",width="500" fluid alt="")

  footer#page-footer.bg-body-light
    div.content.py-0
      div.row.font-size-sm
        div.col-sm-6.order-sm-2.mb-1.mb-sm-0.text-center.text-sm-right
        div.col-sm-6.order-sm-1.text-center.text-sm-left
</template>

<script>
import Axios from "axios";

export default {
  // name: "Prediction App",
  data() {
    return {
      axios: Axios.create({
        headers: {
          "Content-Type": "application/json"
        }
      }),
      lineCount: 0,
      isLoading: false,
      exams: [
        { title: "Serum_Creatinine", value: "" },
        {
          title: "Plasma_Albumin",
          value: ""
        },
        {
          title: "Serum_Sodium",
          value: ""
        },
        {
          title: "Serum_Potassium",
          value: ""
        },
        {
          title: "UP_Qualitative",
          value: ""
        },
        {
          title: "UP_Quantitative",
          value: ""
        },
        {
          title: "Diabetes",
          value: ""
        },
        {
          title: "Hypertension",
          value: ""
        },
        {
          title: "Gender",
          value: ""
        },
        {
          title: "Age",
          value: ""
        },
        {
          title: "Baseline_eGFR",
          value: ""
        }
      ],
      alertMessage: null,
      resultMessage: null,
      result: ""
    };
  },
  methods: {
    onExamUpdated(evt) {
      this.asyncPredictEgfr(this.exams);
    },
    onClickPredict(item) {
      this.asyncPredictEgfr(this.exams);
    },
    asyncPredictEgfr(exams) {
      this.isLoading = true;
      var inputData = Object.assign(
        {},
        ...exams.map(exam => ({ [exam.title]: exam.value }))
      );
      var queryString = Object.keys(inputData)
        .map(key => key + "=" + inputData[key])
        .join("&");
      var inputDone = Object.values(inputData).every(x => x !== "");
      console.log(inputData);
      console.log(inputDone);
      if (inputDone) {
        this.axios
          .post("http://54.248.34.18:8080/predict_egfr2?" + queryString)
          .then(
            function(response) {
              this.result = "data:image/png;base64," + response.data;

              this.alertMessage = null;
              this.isLoading = false;
            }.bind(this)
          )
          .catch(err => {
            console.log(this.err);
            this.alertMessage = err;
            this.isLoading = false;
          });
      } else {
        this.alertMessage = "Fill in all variables";
        this.isLoading = false;
      }
    }
  }
};
</script>

